import { getComments, addComment, deleteComment } from '@/api/article'
import MarkdownIt from 'markdown-it'
import { mapState } from 'vuex'
export default {
  name: 'ArticleComment',
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      comments: [],
      comment: ''
    }
  },
  computed: {
    ...mapState(['user'])
  },
  async mounted () {
    const { data } = await getComments( this.article.slug )
    const md = MarkdownIt()
    data.comments.forEach(comment => comment.body = md.render(comment.body));
    this.comments = data.comments
  },
  methods: {
    async onSubmit () {
      let params = {
        slug: this.article.slug,
        data: {
          comment: {
            body: this.comment
          }
        }
      }
      const { data } = await addComment(params)
      const md = MarkdownIt()
      const { comment } = data
      comment.body = md.render(comment.body)
      this.comments.unshift(comment)
      this.comment = ''
    },
    async deleteCommentFn(id,index) {
      console.log(id)
      let params = {
        slug: this.article.slug,
        id: id
      }
      const { data } = await deleteComment(params)
      console.log(data)
      this.comments.splice(index,1)
    }
  }
}
